:local(.album) {
  picker {
    width: 100%;
  }

  .picker {
    width: 336px;
    margin: 20px auto;
  }

  section {
    background-color: white;
  }

  label {
    width: 33%;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 5px 20px;
    text-align: center;
  }

  .pickerInner {
    display: inline-block;
    width: 336px;
    position: relative;
    top: 0;
    left: calc(50% - 168px);
    margin-bottom: 30px;
  }
}



